import React,{useState,useEffect} from "react";
import {BrowserRouter as Router, Link} from "react-router-dom";
import Particles from 'react-particles-js';

import "../Header.css";



function Header(){
    return<div className="header">
        
        <div className="header-text">
        
        <div className="hello-txt">HELLO, I'M</div>
       <div className="name-txt"><nobr>DIGANTA KR BANIK</nobr></div> 
         <div className="detail-text">,a sophomore at Maulana Abul Kalam Azad University Of Technology<br/>with a knack in web development, devops and competitive programming</div>
      
            
             <Router>
             <a href="#about"><div><i className="fas fa-angle-double-down angle-color"></i></div></a>
             </Router>
           
        </div>
        
        


        <Particles className="part"
        params={{
          particles: {
            number: {
              value: 140,
              density: {
                enable: true,
                value_area: 1000,
              }
            },
            
          },
          fpsLimit: 60,
          interactivity: {
            detectsOn: "canvas",
            events: {
              onClick: {
                enable: true,
                mode: "push",
              },
              onHover: {
                enable: true,
                mode: "repulse",
              },
              resize: true,
            },},
            modes: {
              push: {
                quantity: 4,
                duration:0.2,
              },
              repulse: {
                distance: 200,
                duration: 0.4,
              },
            },
        }}
      />
   
    </div>;
}

export default Header;