import "../global.css";
import ("../ProjectCard.css");

function ProjectCard(props){
    if(props.count%2===0){
        return (<> 
                        <div className="container global-bg projects">
                <div className="project-card">
                    <div class="row pr">
                        <div className="col-lg-5">
                            <img className="img img-fluid pr-img" src={props.imag} alt="Melody Box"/>
                        </div>
                        <div className="col-lg-7 my-auto txt">
                            <div className="pr-head">
                                <h2>{props.name}</h2>
                                <div class="pr-text ">
                                    {props.desc}
                                <br/>
                                Try it out : <a href={props.link} target="_blank" className="a">HERE</a>
                                </div>
                            </div>
                            
                        </div>
                    </div>
                </div>
                </div>
        </>
    );
    }

    else{
        return (
        <>
        <div className="container projects">
                    <div className="project-card">
                        <div class="row pr">
                            <div className="col-lg-7 my-auto txt">
                                <div className="pr-head">
                                    <h2>{props.name}</h2>
                                    <div class="pr-text ">
                                    {props.desc}
                                    <br/>
                                    Try it out : <a href={props.link} target="_blank" className="a">HERE</a>
                                    </div>
                                </div>
                                
                            </div>
                            <div className="col-lg-5">
                                <img className="img img-fluid pr-img" src={props.imag} alt="Melody Box"/>
                            </div>
                        </div>
                    </div>
    
    
                    </div>
            </>
        );
    }

}

export default ProjectCard;
