import './App.css';
import "./global.css";
import 'bootstrap/dist/css/bootstrap.min.css';
import NavBar from "./components/Navbar";
import Header from "./components/Header";
import About from "./components/About";
import Skills from "./components/Skills";
import ProjectCard from "./components/ProjectCard";
import Achievement from "./components/Achievements";
import Projects from "./project.js";
import Footer from "./components/Footer.js";
import ToTheTop from "./components/ToTheTop";
function createProject(projectItem){
    return <ProjectCard name={projectItem.name} count={projectItem.count} desc={projectItem.desc} link={projectItem.link} imag={projectItem.imag}/>
}










function App() {
  return (
    <>
     <NavBar/>
    <Header/>
    <ToTheTop/>
    <About/>
    <Skills/>
    <div id="projects" className="pr-app global-bg">PROJECTS</div>
    {Projects.map(createProject)}
    <div id="achievements" className="pr-app global-bg">ACCOLADES</div>
    <Achievement/>
    <Footer/>
    </>
  );
}

export default App;
